// const moment = require('moment')
import { getList, downLoad, customerdownLoad, customergetList } from '@/api/peoplePackage';
export default {
  name: 'Tab',
  components: {},
  data: function data() {
    return {
      options: ['全部', '手机号', '手机号MD5', 'openId', 'unionId'],
      total: 0,
      is_download: false,
      tableData: [],
      form: {
        is_type: '',
        name: '',
        page: 1,
        size: 10
      },
      pid: 1,
      downloadFunc: customerdownLoad,
      listFunc: customergetList
    };
  },
  created: function created() {
    if (this.$route.path !== '/peoplepack') {
      this.pid = 2;
      this.downloadFunc = downLoad;
      this.listFunc = getList;
    }

    this.getList();
  },
  methods: {
    downLoad: function downLoad(row) {
      this.downloadFunc({
        p_path: row.p_path,
        id: row.id
      }).then(function (res) {
        var aTag = document.createElement('a');
        aTag.download = row.p_path;
        aTag.href = URL.createObjectURL(new Blob([res]));
        aTag.click();
        URL.revokeObjectURL(aTag.href);
      });
    },
    changePage: function changePage() {
      this.getList();
    },
    getList: function getList() {
      var _this = this;

      this.listFunc(this.form).then(function (res) {
        _this.tableData = res.data;
        _this.total = res.count;
        _this.is_download = res.is_download;
      });
    },
    upload: function upload() {
      this.$router.push({
        name: 'upload',
        params: {
          'id': this.pid
        }
      });
    },
    detail: function detail(row) {
      this.$router.push({
        name: 'upload',
        params: {
          packageInfo: row,
          'id': this.pid
        }
      });
    }
  }
};